<template>
  <div>
    <div class="price-collect-panel mt-4">
      <listing-prices-card :the_price_collect="price_collect">
        所属报价单：
      </listing-prices-card>
    </div>

    <div class="member-panel mt-4">
      <listing-member-card :the_member="membery">
        所属客户：
      </listing-member-card>
    </div>

    <div class="listing-time-panel mt-4 fs-12">
      <div class="fs-14">发票时间：</div>
      <div class="card-content pr-4">
        <div class="input-group input-group-sm">
          <input type="text" class="form-control" placeholder="yyyy-dd-mm" v-model="listing_time">
        </div>
      </div>
    </div>

    <div class="listing-time-panel mt-2 fs-12">
      <div class="fs-14">支付方式：</div>
      <div class="card-content pr-4">
        <div class="input-group input-group-sm">
          <select class="form-control" v-model="listing_paymet">
            <option v-for="f in show_paymet" :key="f.val" :value="f.val">{{ f.named }}</option>
          </select>
        </div>
      </div>
    </div>

    <div class="listing-option-panel mt-5 fs-12">
      <div class="fs-14">操作：</div>
      <div class="card-content pr-4">
        <button class="btn btn-light btn-block pices-submit-btn btn-sm" @click="listingPdfView">预览 Pdf</button>
        <button class="btn btn-outline-info btn-block pices-submit-btn btn-sm" @click="submitResult">提交结果</button>
      </div>
    </div>

  </div>
</template>

<script>
import $ from 'jquery'
import service from '@/service'
import view from '@/common/es/view'
import tool from '@/common/es/tool'

import ListingMemberCard from '@/components/Card/ListingMemberCard'
import ListingPricesCard from '@/components/Card/ListingPricesCard.vue'
import Submit from '@/components/Modal/Submit.vue'

export default {
  components: { ListingMemberCard, ListingPricesCard, Submit },
  name: 'ListingPlusRight',
  props: {
    _listing: {
      type: Object
    }
  },
  data() {
    return {
      membery: {},
      price_collect: {},

      show_paymet: view.show_paymet(),

      listing_time: '',
      listing_paymet: view.show_paymet()[0].val,
    }
  },
  created() {
    this.membery = this._listing.membery
    this.price_collect = this._listing.price_collect
  },
  methods: {
    listingPdfView() {
      tool.showModal('listingPdfViewModal')
    },
    submitResult() {
      tool.showModal('submitModal')
    },
    _submitResult() {
      alert('提交')
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

.price-collect-panel, .member-panel, .listing-time-panel, .listing-option-panel
  padding-left: $horizon

</style>


