<template>
    <tr class="prices-item" :class="{ inActive: !selected, 'bg-white': !selected }">
      <th width="5%" scope="row" class="text-input">{{ index + 1 }}</th>
      <td width="10%">{{ f.num }}</td>
      <td width="23%" class="text-left">&nbsp;&nbsp;&nbsp;{{ f.named }}</td>
      <td class="table-input-td " width="20%">
        <div class="input-group input-group-sm float-left listing-num-input">
          <input type="text" class="form-control" :id="'count_' + f.id" :value="f.count" @keyup.enter="_changeNum(f.id, index, 'count_')">
          <div class="input-group-append">
            <span class="input-group-text" id="basic-addon2">{{ unit(f.unit) }}</span>
          </div>
        </div>
      </td>
      <td width="15%" class="text-input pb-0">
        <div class="li-price-icon text-right text-accent">
          $
        </div>
        <div class="li-price-txt pl-1">
          <div class="input-group input-group-sm listing-num-input">
            <input type="text" class="form-control" :id="'price_' + f.id" :value="f.price" @keyup.enter="_changeNum(f.id, index, 'price_')" />
          </div>
        </div>
      </td>
      <td width="10%"></td>
      <td width="10%">{{ (f.price * f.count).toFixed(2) }}</td>
      <td width="7%">
        <div v-show="selected">
          <!-- 已加 -->
          <i class="fa hand listing-table-icon fa-remove" @click="_itemOption(f.id, 'remove')"></i>
        </div>
        <div v-show="!selected">
          <!-- 未加 -->
          <i class="fa hand listing-table-icon fa-plus" @click="_itemOption(f.id, 'add')"></i>
        </div>
      </td>
    </tr>
</template>

<script>
import $ from 'jquery'
import service from '@/service'
import view from '@/common/es/view'
import tool from '@/common/es/tool'
export default {
  name: 'ListingPlusTableItem',
  props: {
    f: {
      type: Object
    },
    index: {
      type: Number
    },
    selected: {
      type: Boolean
    }
  },
  methods: {

    unit(u) {
      return view.ser_unit(u)
    },
    //
    _itemOption(id, option) {
      this.$emit('father_itemOption', id, option)
    },
    //
    _changeNum(id, index, option) {
      this.$emit('father_changeNum', id, index, option)
    }
  }
  
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/choise.sass"

.li-price-icon
  width: 30%
  float: left

.li-price-txt
  width: 70%
  float: left
  margin-top: -4px
  
</style>


