<template>
  <div class="pt-2 bg-white def-shadow-s">
    <table class="table mb-0" v-if="listing_content.length > 0">
      <tbody class="listing-tbody text-center">
        <listing-plus-table-item 
          v-for="(item, i) in listing_content" 
          :key="item.id" 
          :f="item" 
          :index="i" 
          :selected="inList(item.id, select)"

          @father_changeNum="changeNum"
          @father_itemOption="itemOption"
        >
        </listing-plus-table-item>

      </tbody>
    </table>

    <div v-else>No</div>

    <listing-pdf :pdf_listing_content="listing_content" :pdf_member_select="member" :pdf_listing="_listing"></listing-pdf>
  </div>
</template>

<script>
import $ from 'jquery'
import service from '@/service'
import view from '@/common/es/view'
import tool from '@/common/es/tool'
import ListingPdf from '../Pdf/ListingPdf.vue'
import ListingPlusTableItem from './ListingPlusTableItem.vue'

export default {
  components: { ListingPdf, ListingPlusTableItem },
  name: 'ListingPlusLeft',
  props: {
    _listing: {
      type: Object
    }
  },  
  data() {
    return {
      select: [ 1 ],
      listing_content: [],
      member: {}
    }
  },
  created() {
    this.member = this._listing.membery

    const res = this._listing.content.map((e) => {
      e['count'] = 0
      return e
    })
    this.listing_content = res

  },
  methods: {
    unit(u) {
      return view.ser_unit(u)
    },
    inList(n, L) {
      return tool.inList(n, L)
    },

    //
    itemOption(id, option) {
      if (option == 'add') {
        this.addItem(id)
      } 
      else if (option == 'remove') {
        this.removeItem(id)
      }
    },
    removeItem(id) {
      const i = tool.indexList(id, this.select)
      if (this.select.length < 2) {
        tool.showToast('至少选择一项菜品。')
      } else {
        this.select.splice(i, 1)
      }
    },
    addItem(id) {
      const i = tool.indexList(id, this.select)
      if (i < 0) {
        this.select.push(id)
      }
    },
    //
    changeNum(id, index, option) {
      const val = $('#' + option + id).val()

      if (option == 'count_') {
        this.listing_content[index].count = val
      } else if (option == 'price_') {
        this.listing_content[index].price = val
      }
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/choise.sass"

</style>


