<template>
  <div class="modal fade" tabindex="-1" role="dialog" id="listingPdfViewModal">
    <div class="modal-dialog modal-lg modal-dialog-centered modal-dialog-scrollable">
      <div class="modal-content">
        
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-12 text-center fs-12 mt-2">
              <pdf-header>
                发票
              </pdf-header>
            </div>

            <div class="col-sm-12">
              <table class="pdf-table-outter">
                <tr>
                  <td width="70%">
                    <pdf-invoice-member :the_member="pdf_member_select"></pdf-invoice-member>
                  </td>
                  <td>
                    <pdf-invoice-msg
                      :listing_num="pdf_listing.num"
                      :listing_time="pdf_listing.listing_time"
                      :listing_payment="ser_listing_payment(pdf_listing.pay_way)"
                      :member_num="pdf_member_select.num"
                    ></pdf-invoice-msg>
                  </td>
                </tr>
              </table>
            </div>

            <div class="col-sm-12">
              <pdf-invoice-freight :lising_content="pdf_listing_content"></pdf-invoice-freight>
            </div>

          </div>
          
          <pdf-footer :member="pdf_member_select"></pdf-footer>
        </div>

        <div class="modal-footer">
          <button class="btn btn-outline-secondary btn-sm" data-dismiss="listingPdfViewModal">关闭</button>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
import service from '@/service'
import view from '@/common/es/view'
import tool from '@/common/es/tool'
import PdfFooter from '@/components/Pdf/PdfFooter.vue'
import PdfHeader from '@/components/Pdf/PdfHeader.vue'
import PdfInvoiceMember from '@/components/Pdf/Invoice/PdfInvoiceMember.vue'
import PdfInvoiceMsg from '@/components/Pdf/Invoice/PdfInvoiceMsg.vue'
import PdfInvoiceFreight from '@/components/Pdf/Invoice/PdfInvoiceFreight.vue'

export default {
  name: 'ListingPdf',
  components: { 
    PdfFooter,
    PdfHeader,
    PdfInvoiceMember,
    PdfInvoiceMsg,
    PdfInvoiceFreight
  },
  props: {
    pdf_listing_content: {
      type: Array
    },
    pdf_listing: {
      type: Object
    },
    pdf_member_select: {
      type: Object
    }
  },
  created() {
    
  },
  methods: {
    ser_listing_payment(v) {
      return view.ser_listing_paymet(v)
    },
    invoicePrint() {
      // tool.go()
    },
    getPricesTime() {
      return '2020/12/16'
    },
    getPricesNum() {
      return '003AC'
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"

</style>


