<template>
  <div class="d-flex justify-content-between full-content bg-content">
    <div class="content-top-panel">
      <listing-plus-top></listing-plus-top>
    </div>

    <div class="content-left-panel">
      <listing-plus-left :_listing="listing"></listing-plus-left>
    </div>

    <div class="content-right-panel def-shadow">
      <listing-plus-right :_listing="listing" ref="lpr"></listing-plus-right>
    </div>

    <submit @submitConfirm="_submitResult"></submit>
  </div>
</template>

<script>
import service from '@/service'
import view from '@/common/es/view'
import tool from '@/common/es/tool'

import ListingPlusLeft from './Left/ListingPlusLeft.vue'
import ListingPlusRight from './Right/ListingPlusRight.vue'
import ListingPlusTop from './Top/ListingPlusTop.vue'
import Submit from '@/components/Modal/Submit.vue'

export default {
  components: { ListingPlusTop, ListingPlusLeft, ListingPlusRight, Submit,  },
  name: 'ListingPlus',
  data() {
    return {
      listing: service.listingList[0]
    }
  },
  methods: {
    _submitResult() {
      this.$refs.lpr._submitResult()
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"

</style>


