<template>
  <table class="pdf-table fs-12 w-100 pdf-table-freight mt-2 text-center">
    <thead>
      <tr class="">
        <th width="49">项号</th>
        <th>货号</th>
        <th>描述</th>
        <th>数量</th>
        <th>单价</th>
        <th width="49">折扣</th>
        <th>金额</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(v, i) in lising_content" :key="v.id">
        <td>{{ i + 1 }}</td>
        <td>{{ v.num }}</td>
        <td>{{ v.named }}</td>
        <td>
          <div style="width: 60%" class="text-right float-left">{{ v.count }}&nbsp;</div>
          <div style="width: 40%" class="text-left float-left">{{ unit(v.unit) }}</div>
        </td>
        <td class="text-right">{{ v.price }}</td>
        <td>&nbsp;</td>
        <td class="text-right">{{ (parseFloat(v.price) * parseFloat(v.count)).toFixed(2) }}</td>
      </tr>

      <tr class="pdf-table-freight-footer br-t-pdf">
        <td>备注</td>
        <td colspan="3">
        </td>
        <td>项目合计</td>
        <td>HK$</td>
        <td></td>
      </tr>
      <tr class="pdf-table-freight-footer">
        <td></td>
        <td colspan="3">
        </td>
        <td>附加费</td>
        <td></td>
        <td></td>
      </tr>
      <tr class="pdf-table-freight-footer">
        <td></td>
        <td colspan="3">
        </td>
        <td>折扣</td>
        <td></td>
        <td></td>
      </tr>
      <tr class="pdf-table-freight-footer">
        <td></td>
        <td colspan="3">
        </td>
        <td>统计</td>
        <td>HK$</td>
        <td class="text-right">{{ result_price }}</td>
      </tr>

    </tbody>
  </table>
</template>

<script>
import util from '@/common/es/view'
import tool from '@/common/es/tool'
export default {
  name: "PdfInvoiceFreight",
  props: {
    lising_content: {
      type: Array
    }
  },
  computed: {
    freight_price() {

    },
    result_price() {
      let res = 0.00
      for (const v of this.lising_content) {
        const freight_price = v.price
        const freight_count = v.count
        const sumR = tool.floatNum.floatMul(freight_price, freight_count)
        res = tool.floatNum.floatAdd(sumR, res)
      }
      return res
    }
  },
  methods: {
    unit(u) {
      return util.ser_unit(u)
    },
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"

</style>


