        <td>{{ listing_payment }}</td>
      </tr>
      <tr>
        <td>销售员：</td>
        <td></td>
      </tr>
    </tbody>
  </table>
</template>

<script>
export default {
  name: 'PdfInvoiceMsg',
  props: {
    listing_num: {
      type: String
    },
    listing_time: {
      type: String
    },
    member_num: {
      type: String
    },
    listing_payment: {
      type: String
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"

</style>


